<template>
    <div id="app">
        <van-nav-bar title="跑者信息"/>
        <van-cell-group>
            <van-field
                v-model="name"
                clearable
                label="姓名"
                placeholder="请输入姓名"
            />
            <van-cell title="性别" is-link :value="gender" @click="selectgender"/>
            <van-cell title="出生日期" is-link :value="Csrq" @click="selectCsrq"/>
            <van-field
                v-model="phone"
                clearable
                label="手机号"
                placeholder="请输入手机号"
            />
            <van-field
                v-model="email"
                clearable
                label="邮箱"
                placeholder="请输入邮箱"
            />
            <van-cell title="证件类型" is-link :value="idType" @click="selectidType"/>
            <van-field
                v-model="idNo"
                clearable
                label="证件号"
                placeholder="请输入证件号"
            />
            <van-field
                v-model="emergencyContactPerson"
                clearable
                label="紧急联系人"
                placeholder="请输入证紧急联系人"
            />
            <van-field
                v-model="emergencyContactTel"
                clearable
                label="紧急联系人电话"
                placeholder="请输入紧急联系人电话"
            />
            <van-field
                v-model="nationality"
                clearable
                label="紧急联系人关系"
                placeholder="请输入紧急联系人关系"
            />
            <van-cell title="血型" is-link :value="bloodType" @click="selectbloodType"/>
            <van-cell title="T恤尺码" is-link :value="tShirtSize" @click="selecttShirtSize"/>
            <van-cell>
                <van-button size="large" type="primary" @click="postdata">提交</van-button>
            </van-cell>
        </van-cell-group>
        <van-popup v-model="birthPicker" position="bottom">
            <van-datetime-picker
                v-model="birth"
                type="date"
                @confirm="checkbirth"
                @cancel="birthPicker=false"
            />
        </van-popup>
        <van-popup v-model="genderPicker" position="bottom">
            <van-picker :columns="pickColumns.gender" @change="onChangegender"/>
        </van-popup>
        <van-popup v-model="idTypePicker" position="bottom">
            <van-picker :columns="pickColumns.idType" @change="onChangeidType"/>
        </van-popup>
        <van-popup v-model="bloodTypePicker" position="bottom">
            <van-picker :columns="pickColumns.bloodType" @change="onChangebloodType"/>
        </van-popup>
        <van-popup v-model="tShirtSizePicker" position="bottom">
            <van-picker :columns="pickColumns.tShirtSize" @change="onChangetShirtSize"/>
        </van-popup>
    </div>
</template>
<script>
    import { saveInfo } from '../api/axios.js';
    import moment from 'moment';
    export default {
        name: 'Layout',
        data () {
            return {
                birthPicker: false,
                genderPicker: false,
                idTypePicker: false,
                bloodTypePicker: false,
                tShirtSizePicker: false,
                name: '',
                gender: '',
                birth: null,
                phone: '',
                email: '',
                idType: '',
                idNo: '',
                nationality: '',
                emergencyContactPerson: '',
                emergencyContactTel: '',
                bloodType: '',
                tShirtSize: '',
                personalBest: '',
                runnerType: '',
                pickColumns: {
                    gender: ["男", "女"],
                    idType: ["身份证号", "护照"],
                    bloodType: ["A型", "B型", "AB型", "O型"],
                    tShirtSize: ["S", "M" , "L", "XL", "XXL", "XXXXL"]
                }
            }
        },
        computed: {
            Csrq () {
                return this.birth!=null?moment(this.birth).format('YYYY-MM-DD'):'';
            }
        },
        methods: {
            selectCsrq () {
                this.birthPicker = true;
            },
            selectgender () {
                this.genderPicker = true;
            },
            selectidType () {
                this.idTypePicker = true;
            },
            selectbloodType () {
                this.bloodTypePicker = true;
            },
            selecttShirtSize () {
                this.tShirtSizePicker = true;
            },
            onChangegender (picker, value, index) {
                this.gender = value;
            },
            onChangeidType (picker, value, index) {
                this.idType = value;
            },
            onChangebloodType (picker, value, index) {
                this.bloodType = value;
            },
            onChangetShirtSize (picker, value, index) {
                this.tShirtSize = value;
            },
            checkbirth () {
                console.log(this.birth)
                this.birthPicker = false;
            },
            postdata () {
                let formData = {
                    'name': this.name,
                    'gender': this.gender,
                    'birth': this.birth,
                    'phone': this.phone,
                    'email': this.email,
                    'idType': this.idType,
                    'idNo': this.idNo,
                    'bloodType': this.bloodType,
                    'nationality': this.nationality,
                    'emergencyContactPerson': this.emergencyContactPerson,
                    'emergencyContactTel': this.emergencyContactTel,
                    'tShirtSize': this.tShirtSize,
                    'personalBest': this.personalBest,
                    'runnerType': this.runnerType
                };
                saveInfo(formData).then( (res) => {
                    console.log(res)
                });
            }
        }
    }
</script>
<style lang="less">
    .van-cell {
        .van-cell__title {
            max-width: 90px;
        }
        .van-cell__value {
            text-align: left;
        }
    }

</style>



